import React from 'react';
import { default as MuiStepper, StepperProps } from '@mui/material/Stepper';

const Stepper = React.forwardRef<HTMLElement, StepperProps>( ( props, ref ) => {
	return (
		<MuiStepper
			{ ...props }
			ref={ ref as React.MutableRefObject<null> }
		/>
	);
} ) as React.FC<StepperProps>;

export default Stepper;
